import {
	mapState,
	mapMutations
} from 'vuex';
export default {
	data() {
		return {
			searchList: [],
			keyword: '',
			isLoad: false
		}
	},
	methods: {
		...mapMutations(['addHistory', 'emptyHistory']),
		searchInput(name) {
			// 显示加载图标
			this.isLoad = true
			this.keyword = name
			// 清空搜索值时，清除搜索结果列表
			if (!name.trim()) {
				this.searchList = []
				// 在清空搜索框时，最后一个字符，还会发送请求
				clearTimeout(this.timer)
				return
			}

			if (this.timer) clearTimeout(this.timer);
			this.timer = setTimeout(() => {
				this.getSearch(name)
				this.timer = null;
			}, 500);
		},
		getSearch(name) {

			this.$api.getSearchC({
				name
			}).then(res => {
				this.isLoad = false
				this.searchList = res.result.result
			})
		},
		addRecordBox() {
			// 如果历史记录中已经存在，直接返回，不在存储
			if (this.historyList.includes(this.keyword)) return
			this.addHistory(this.keyword)
		},
		againSearch(item) {
			this.keyword = item
			this.getSearch(item)
		},
		clearHistory() {
			// 将store中的historyList设置为空数组
			this.emptyHistory()
		}

	},
	computed: {
		...mapState(['historyList'])
	}
}
